import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { MuiTabInterface } from '../../../../shared/interfaces/mui-tab.interface';

@Component({
    selector: 'mui-flex-popups-page',
    templateUrl: './mui-flex-popups-page.component.html',
    styleUrls: ['./mui-flex-popups-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class MuiFlexPopupsPageComponent implements OnInit {

    public page = 'Angular';

    public activeIndex = 1;

    public activeItemIndex = 1;

    public items: MuiTabInterface[] = [
        {
            caption: 'Angular',
            routerLink: 'angular-content',
        },
        {
            caption: 'JQuery',
            routerLink: 'jquery-content',
        },
    ];

    constructor() { }

    ngOnInit(): void {
    }

    public selectPage(page: string): void {
        this.page = page;
    }

    setProperties(): void {

    }

}
